    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 16pt;
    max-height: 100rem;
    padding: 2rem;
    text-align: start;
    width: 30rem;
}

.message {
    max-width: 25rem;
}

.message.myMessage {
    align-self: end;
}
